body {
  user-select: none; /* 禁止选择文本 */
}

.Column {
  display: flex;
  flex-direction: column;
}

.Center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.TopAppBar {
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  color: #F1F1F1;
  background-color: #192836;
  box-shadow: #000000 0 2px 5px;
  font-size: 22px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Brand {
  display: flex;
  align-items: center;
  margin: 10px;
}

.Logo {
  width: 40px;
  height: 40px;
  margin-right: 15px;
}

.IconButton {
  width: 20px;
  height: 20px;
  margin: 0 10px;
  border-radius: 50%;
  padding: 6px;
  background-color: #F1F1F1;
}

.IconButton:active:hover {
  background-color: #C1C1C1;
}

.IconButton:hover {
  background-color: #C1C1C1;
}

.TextButton {
  font-size: medium;
  margin: 0 20px;
}

.Content {
  color: #F1F1F1;
  background-color: #131e29;
  min-height: 100vh;
}

.Slogan {
  width: 100%;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 240%;
  font-weight: bold;
  text-align: center;
  text-shadow: #000000 0 4px 7px;
}

.Products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;
  gap: 20px;
  padding: 20px 0;
  margin: 0 20px;
}

.Product {
  width: 300px;
  display: flex;
  background-color: #192836;
  box-shadow: #000000 0 2px 5px;
  padding: 20px;
  border-radius: 20px;
}

.Product:active:hover {
  background-color: #223548;
  border-radius: 20px;
}

.Product:hover {
  background-color: #223548;
  border-radius: 20px;
}

.Product img {
  width: 70px;
  height: 70px;
  background-color: #F1F1F1;
  border-radius: 50%;
}

.ProductChar {
  width: 70px;
  height: 70px;
  background-color: #F1F1F1;
  border-radius: 50%;
  color: #192836;
  font-size: 200%;
  font-weight: bolder;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ProductDesc {
  font-size: 80%;
  margin-top: 5px;
  color: #EEEEEE;
}